import React, { useState, useEffect } from "react";
const API = () => {
  const urlFacts = "https://catfact.ninja/fact";
  const urlImages = "https://api.thecatapi.com/v1/images/search";
  const [facts, setFacts] = useState([]);
  const [images, setImages] = useState([]);

  useEffect(() => {
    fetch(urlFacts)
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        setFacts([data]);
      });
  }, []);
  useEffect(() => {
    fetch(urlImages)
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        setImages(data);
      });
  }, []);

  function reloadWindow() {
    window.location.reload(false);
  }
  console.log(images);
  return (
    <div className="card">
      {/* facts  */}
      {facts.map((fact) => (
        <div>
          {/* images  */}
          {images.map((img) => (
            <img src={img.url} />
          ))}
          <div key={fact.id} className="info">
            <p style={{ marginBottom: "24px" }}>{fact.fact}</p>
            <p style={{ opacity: "0.6", fontSize: "0.8rem" }}>
              {fact.length} Letters
            </p>
            <button onClick={reloadWindow}>Next</button>
          </div>
        </div>
      ))}
    </div>
  );
};
export default API;
